"use client";

import { useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import animationData from "@/shared/utils"
import Lottie from "lottie-react";

interface BuyNowModalProps {
  onClose: () => void;
}

const BuyNowModal = ({ onClose }: BuyNowModalProps) => {
  useEffect(() => {
    const timer = setTimeout(onClose, 3000);
    return () => clearTimeout(timer);
  }, [onClose]);

  return (
    <AnimatePresence>
      <motion.div className="fixed inset-0 flex items-start justify-center mt-24 px-4 z-50 pointer-events-none">
        <div className="bg-white rounded-3xl shadow p-8 border border-dashed border-gray-300 max-w-md w-full">
          <div className="flex items-center gap-4">
            <Lottie
              animationData={animationData}
              renderer="svg"
              loop={false}
              autoplay
            />

            {/* Text */}
            <div className="flex-1">
              <h3 className="text-xl font-bold text-teal-700 mb-1">
                Congratulations
              </h3>
              <p className="text-gray-700">Item has been added to your cart</p>
            </div>
          </div>
        </div>
      </motion.div>
    </AnimatePresence>
  );
};

export default BuyNowModal;
